<template>
    <div id="edit-teachers">
        <div>
            <h3>Edit Teacher</h3>
        </div>
        <div class="card-body">
            <form v-on:submit.prevent="updateItem">
                <div class="form-group">
                    <label>First Name:</label>
                    <input type="text" class="form-control" v-model="newItem.firstname"/>
                </div>
                <div class="form-group">
                    <label>Last Name:</label>
                    <input type="text" class="form-control" v-model="newItem.lastname" />
                </div>
                 <div>
                    
                    <b-form-select v-model="newItem.Sex" class="mb-3">
                      <template slot="first">
                         <option :value="null" disabled>-- Sex --</option>
                      </template>
                      <option value="Male">Male</option>
                      <option value="Female">Female</option>
                    </b-form-select>
                </div>
                <div class="form-group">
                    <input type="submit" class="btn btn-primary" value="Update Teacher"/>
                    <router-link to="/Teachers" class="btn grey">Cancel</router-link>
                </div>
            </form>
        </div>
    </div>
</template>

<script>

import { db } from '../db';

export default {
    name: 'edit-teachers',
    data() {
        return {
            newItem: {}
        }
    },
    firebase: {
        items: db.ref('Users/Teachers'),
        itemsObj: {
            source: db.ref('Users/Teachers'),
            asObject: true
        }
    },
    created() {
        let item = this.itemsObj[this.$route.params.id]
        this.newItem = {
            firstname: item.firstname,
            lastname: item.lastname,
            email: item.email,
            password: item.password,
            Sex: item.Sex
        }
    },
    methods: {
        updateItem() {
            this.$firebaseRefs.items.child(this.$route.params.id).set(this.newItem);
            this.$router.push('/Teachers')
        }
    }
}
</script>



