<template>
    <div id="edit-students">
        <div>
            <h3>Edit Student</h3>
        </div>
        <div class="card-body">
            <form v-on:submit.prevent="updateItem">
                <div class="form-group">
                    <label>First Name:</label>
                    <input type="text" class="form-control" v-model="newItem.firstName"/>
                </div>
                <div class="form-group">
                    <label>Last Name:</label>
                    <input type="text" class="form-control" v-model="newItem.lastName" />
                </div>
                <div>
                    <b-form-select v-model="newItem.Sex" class="mb-3">
                      <template slot="first">
                         <option :value="null" disabled>-- Sex --</option>
                      </template>
                      <option value="Male">Male</option>
                      <option value="Female">Female</option>
                    </b-form-select>
                    <b-form-select v-model="newItem.gradeLevel" class="mb-3">
                      <option :value="null" disabled >-- Grade Level --</option>
                      <option value="3">3</option>
                      <option value="4">4</option>
                      <option value="5">5</option>
                      <option value="6">6</option>
                    </b-form-select>
                  </div>
                <div class="form-group">
                    <input type="submit" class="btn btn-primary" value="Update Student"/>
                    <router-link to="/students" class="btn grey">Cancel</router-link>
                </div>
            </form>
        </div>
    </div>
</template>

<script>

import { db } from '../db';

export default {
    name: 'edit-students',
    data() {
        return {
            newItem: {}
        }
    },
    firebase: {
        items: db.ref('Users/Students'),
        itemsObj: {
            source: db.ref('Users/Students'),
            asObject: true
        }
    },
    created() {
        let item = this.itemsObj[this.$route.params.id]
        this.newItem = {
            firstName: item.firstName,
            lastName: item.lastName,
            email: item.email,
            password: item.password,
            accountType: item.accountType,
            ScoreA: item.ScoreA,
            ScoreP: item.ScoreP,
            ScoreW: item.ScoreW,
            gradeLevel: item.gradeLevel,
            Sex: item.Sex
        }
    },
    methods: {
        updateItem() {
            this.$firebaseRefs.items.child(this.$route.params.id).set(this.newItem);
            this.$router.push('/students')
        }
    }
}
</script>
<style scoped>

.mb-3{

    width: 530px;
}

</style>


