<template>
    <div id="newStudnts">
        <div>
            <h3>Add New Student</h3>
        </div>
        <div class="card-body">
            <form v-on:submit.prevent="register">
                <div class="form-group">
                    <label>First Name:</label>
                    <input type="text" class="form-control" v-model="firstname"/>
                </div>
                <div class="form-group">
                    <label>Last Name:</label>
                    <input type="text" class="form-control" v-model="lastname"/>
                </div>
                  <div>
                    
                    <b-form-select v-model="Sex" class="mb-3">
                      <template slot="first">
                         <option :value="null" disabled>-- Sex --</option>
                      </template>
                      <option value="Male">Male</option>
                      <option value="Female">Female</option>
                    </b-form-select>
                    <b-form-select v-model="gradeLevel" class="mb-3">
                      <option :value="null" disabled >-- Grade Level --</option>
                      <option value="3">3</option>
                      <option value="4">4</option>
                      <option value="5">5</option>
                      <option value="6">6</option>
                    </b-form-select>
                  </div>
                <div class="form-group">
                    <label>Email:</label>
                    <input type="text" class="form-control" v-model="email"/>
                </div>
                <div class="form-group">
                    <label>Password:</label>
                    <input type="password" class="form-control" v-model="password"/>
                </div>
                <div class="form-group">
                    <label>Confirm Password:</label>
                    <input type="password" class="form-control" v-model="password_confirmation"/>
                </div>
                 
                <div class="form-group">
                    <input type="submit" class="btn btn-primary" value="Add Student"/>
                    <router-link to="/studnts" class="btn grey">Cancel</router-link>
                </div>
               
            </form>
        </div>
    </div>
</template>

<script>

import { db } from '../db';
import firebase from 'firebase'
let userRef = firebase.database().ref('Users/Students')
let accountsRef = firebase.database().ref('Users/Accounts')
export default {
    name: 'newStudnts',
    data() {
        return {
            firstname: '',
            lastname: '',
            email: '',
            password: '',
            password_confirmation: '',
            errors: [],
            ScoreA:'',
            ScoreP:'',
            ScoreW:'',
            addedBy: '',
            Sex:null,
            gradeLevel: null
        }
    },
    
    methods: {
      register() {
    let errors = []
        var email = this.email;
        var password = this.password;
        let teacher = firebase.auth().currentUser.email
        if(this.isFormValid){
          firebase.auth().createUserWithEmailAndPassword(email, password)
          .then((data) => {
           console.log(data.user)

            let param = {
              firstName: this.firstname,
              lastName: this.lastname,
              accountType: 2,
              email: this.email,
              password: this.password,
              uid: data.user.uid,
              ScoreA : 0,
              ScoreP : 0,
              ScoreW : 0,
              addedBy : teacher,
              Sex : this.Sex,
              gradeLevel : this.gradeLevel

            }
            this.saveUserToUsersRef(param),
            this.saveUserToAccountsRef(param),
            this.$router.go({ path: this.$router.path});
            this.logOut()
            
            
            
          }).catch((error) => {
          
          })
        }
      },
      saveUserToUsersRef(user){
        return userRef.child(user.uid).set({
          firstName: user.firstName,
          lastName: user.lastName,
          accountType : user.accountType,
          email: user.email,
          password: user.password,
          ScoreA : user.ScoreA,
          ScoreP : user.ScoreP,
          ScoreW : user.ScoreW,
          addedBy : user.addedBy,
          Sex : user.Sex,
          gradeLevel : user.gradeLevel
        })  
      },
       saveUserToAccountsRef(user){
        return accountsRef.child(user.uid).set({
          accountType : user.accountType,
          email: user.email,
          password: user.password

        })  
      },

      isEmpty(){
     
        if(this.name.length ==  0 || this.email.length == 0 || this.password.length == 0 || this.password_confirmation.length == 0){
          return true
        }
        return false
      },
      passwordValid(){
  
        if(this.password.length < 6 || this.password_confirmation.length < 6){

          return false
        }
        if(this.password !== this.password_confirmation){
          return false
        }
        return true
      },
      isFormValid(){

        if(this.isEmpty()){
          this.errors.push('Empty Field')
          return false
        }
        if(!this.passwordValid){
          this.errors.push('password incorrect')
          return false
        }
        return true
    },
    logOut(){
    firebase.auth().signOut().then(() => {
      this.$router.go({ path: this.$router.path });
    })
  }
  },
     created(){
          console.log(this.$router)
       }
  

}
</script>
<style scoped>

.mb-3{

    width: 530px;
}

</style>


